<template>
  <container :title="title">
    <list :editRoute="ruleRoute" :isPager="false">
      <thead slot="thead">
      <tr>
        <th>{{lang.id}}</th>
        <th>{{lang.name}}</th>
        <th>{{lang.route}}</th>
        <th>{{lang.method}}</th>
        <th>{{lang.type}}</th>
        <th>{{lang.status}}</th>
        <th>{{lang.sorts}}</th>
        <th width="125">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="(vol,index) in list">
        <td>{{vol.id}}</td>
        <td>{{vol.name}}</td>
        <td>{{vol.route}}</td>
        <td>{{vol.method}}</td>
        <td>{{$t('auth_rule_type.'+vol.type)}}</td>
        <v-td type="status" :value="vol.status" />
        <td>{{vol.sorts}}</td>
        <v-td type="opt" :value="vol.id"
              edit-route="auth_rule_edit" />
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd, Checkbox} from "@/components"

  export default {
    components: {VTd, List, Container,Checkbox},
    data(){
      return{
        title:this.$t('auth_rule.index'),
        lang:this.$t('auth_rule'),
        ruleRoute:{
          name:'auth_rule_edit'
        }
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      }
    },
    mounted(){
      const url = '/api/user/group/rule/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



